.radio {
  display: flex;
  padding: 2rem;
  margin: 1rem;
  border: 2px solid #FFFFFF;
}
.radio:hover {
  background: blanchedalmond;
}
.radio.answered {
  pointer-events: none;
}

.radio.answered.checked {
  border: 2px solid #A5A79A;
  transition: border 0.3s
}
.radio.answered.checked.correct {
  border: 2px solid #36C2B4
}

.radio_input {
  display: none;
}

.radio_indicator {
  border: 1px solid #A5A79A;
  border-radius: 50%;
  height: 2rem;
  width: 2rem;
  min-width: 2rem;
  line-height: 2rem;
  font-size: 0.1rem;
  text-align: center;
  vertical-align: middle;
}
.radio_indicator>svg {
  opacity: 0;
  transition: opacity 0.3s;
}

.radio_indicator>svg.checkmark{
  opacity: 1;
  fill: #36C2B4;
}

.radio_indicator>svg.exmark{
  opacity: 1;
  fill: #A5A79A
}

.radio_label {
  font-size: 1.5rem;
  line-height: 2rem;
  margin-left: 5rem;
  word-break: break-word;
  text-align: left;
}