
.checkbox {
  display: flex;
  padding: 2rem;
  margin: 1rem;
  border: 2px solid #FFFFFF;
}

.checkbox:hover {
  background: blanchedalmond;
}

.checkbox.answered {
  pointer-events: none;
}
.checkbox.checked.answered {
  border: 2px solid #A5A79A;
  transition: border 0.3s
}
.checkbox.checked.answered.correct {
  border: 2px solid #36C2B4;
  transition: border 0.3s
}

.cb_input {
  display: none
}

.cb_indicator {
  border: 1px solid #A5A79A;
  height: 2rem;
  width: 2rem;
  min-width: 2rem;
  line-height: 2rem;
  font-size: 0.1rem;
  text-align: center;
  vertical-align: middle;
}


.cb_indicator>svg {
  opacity: 0;
  transition: opacity 0.3s;
}

.cb_indicator>svg.checkmark{
  opacity: 1;
  fill: #36C2B4;
}

.cb_indicator>svg.exmark{
  opacity: 1;
  fill: #A5A79A
}

.cb_label {
  font-size: 1.5rem;
  line-height: 2rem;
  margin-left: 5rem;
  word-break: break-word;
  text-align: left;
}